import React, { Component } from 'react'
import './api.css'
import { Link } from 'react-router-dom'


export class api extends Component {

    constructor() {
        super()

        this.state = {
            items: [],
            MainTitle: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        const url = 'https://api.nivesh.com/API/getSchemesDataV2';
        const postBody = {
            ProductCategoryId: this.props.productCategoryId,
            ClientCode: "",
            LanguageId: 1,
            device: "",
            AMCCode: "",
            SebiCategoryId: this.props.sebiCategoryId ? this.props.sebiCategoryId : "",
            SebiSubCategoryId: this.props.sebiSubCategoryId ? this.props.sebiSubCategoryId : "",
            DefaultProductId: this.props.defaultProductId ? this.props.defaultProductId : ""
        };
        const requestMetadata = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(postBody)
        };

        fetch(url, requestMetadata)
            .then(response => response.json())
            .then(data => {
                this.setState({ isLoaded: true, items: data.ObjectResponse.SchemeDataList, MainTitle: data.ObjectResponse.TitleResponse });
            }).catch(error => {
                console.log(error);
            });
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.productCategoryId !== this.props.productCategoryId) {
            return true;
        } else if (nextProps.sebiCategoryId !== this.props.sebiCategoryId) {
            return true;
        } else if (nextProps.sebiSubCategoryId !== this.props.sebiSubCategoryId) {
            return true;
        } else if (nextProps.defaultProductId !== this.props.defaultProductId) {
            return true;
        }
        else if (nextState.MainTitle?.Title !== this.state.MainTitle?.Title) {
            return true;
        }
        else if (JSON.stringify(nextState.items) !== JSON.stringify(this.state.items)) {
            return true;
        }

        return false;
    }

    componentDidUpdate() {
        const url = 'https://api.nivesh.com/API/getSchemesDataV2';
        const postBody = {
            ProductCategoryId: this.props.productCategoryId,
            ClientCode: "",
            LanguageId: 1,
            device: "",
            AMCCode: "",
            SebiCategoryId: this.props.sebiCategoryId ? this.props.sebiCategoryId : "",
            SebiSubCategoryId: this.props.sebiSubCategoryId ? this.props.sebiSubCategoryId : "",
            DefaultProductId: this.props.defaultProductId ? this.props.defaultProductId : "",
        };
        const requestMetadata = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(postBody)
        };

        this.setState({ isLoaded: false });
        fetch(url, requestMetadata)
            .then(response => response.json())
            .then(data => {
                this.setState({ isLoaded: true, items: data.ObjectResponse.SchemeDataList, MainTitle: data.ObjectResponse.TitleResponse });
            }).catch(error => {
                console.log(error);
                this.setState({ items: null });
            });
    }


    render() {
        const { isLoaded, items, MainTitle } = this.state;

        if (!isLoaded) return <div><h1> Fund Tables are Loading </h1></div>;
        return (
            <div className="api">
                <h2>{MainTitle && MainTitle[1]?.Title}</h2>

                {items && items.map((item) => (

                    <div className='api-flex' key={item.SchemeCode}>

                        <div className="flex-container">
                            <div className="flex-item fg3">{item.SchemeName}</div>
                            <div className="flex-item"><a href={`https://app.nivesh.com/Home/Index?SchemeCode=${item.SchemeCode}&selectedcategoryId=${this.props.productCategoryId}`} target="_blank" className="api-button">Invest Now</a></div>
                        </div>
                        <div className="flex-container1">
                            <div className="flex-item1">1 Year</div>
                            <div className="flex-item1">2 Year</div>
                            <div className="flex-item1">3 Year</div>
                            <div className="flex-item1">5 Year</div>
                        </div>
                        <div className="flex-container2">
                            <div className="flex-item1">{item.OneYearReturn}%</div>
                            <div className="flex-item1">{item.TwoYearReturn}%</div>
                            <div className="flex-item1">{item.ThreeYearReturn}%</div>
                            <div className="flex-item1">{item.FiveYearReturn}%</div>
                        </div>
                    </div>
                ))}
            </div>
        )
    }
}

export default api
